.dbtn {
  background: #44af6a none repeat scroll 0% 0%;
  width: 109px;
  height: 50px;
  border-radius: 17px;
  display: flex;
  place-content: center;    
  font-weight: bold;
}

.dbtn-inner {
  font-weight: bold;
  color: white;
  display: flex;
  align-items: center;
}

.componentTitle {
  font-weight: bold;
  font-size: larger;
}

.yellowdot {
  height: 15px;
  width: 15px;
  background-color: yellow;
  border-radius: 50%;
  display: inline-block;
  padding:3px
}

.dot {
  height: 15px;
  width: 15px;
  border-radius: 50%;
  display: inline-block;
  padding:3px
}

/* bootstrap override */

.table td, .table th{
  border-top: none;
  
}

.table td {
  color: #9E9E9E;
}

.card{
  background: floralwhite;
}

.cardTitle{
  font-weight: bold;
  font-size: larger;
}